  background-color: #ffc60c;
  display: flex;
  flex-direction: column;
  gap: 3rem;
  padding-top: 3rem;
  padding-bottom: 3rem;

  header {
    background-color: #ffde73;
    display: flex;
    justify-content: center;

    nav {
      display: flex;
      gap: 1rem;

